<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
        <div style="height: 10px"></div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
        </table-component>
    </div>
</template>

<script>
    import TopButtons from './components/TopButtons'
    import {getWorkPlanList, deleteWorkPlan} from '@/api/departmentWorkPlan'
    import tableComponent from '@/components/table'
    import filterComponent from '@/components/filter/Index'

    export default {
        components: {
            TopButtons, filterComponent, tableComponent
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                formId: 0,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '规划名称',
                        dataIndex: 'title',
                        key: 'title',
                    },
                    {
                        title: '规划目标党支部',
                        dataIndex: 'department.name',
                        key: 'department.name',
                    },
                    {
                        title: '发布人',
                        dataIndex: 'initiate_user.name',
                        key: 'initiate_user.name',
                    },
                    {
                        title: '发布时间',
                        dataIndex: 'published_at',
                        key: 'published_at',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            check: {visible: true, disable: false,},
                        }
                    }
                ],
                tableRequest: getWorkPlanList,
                filterFields:[
                    {label:'党支部',field:'department_id'},
                    {label:'开始时间',field:'start_timestamp'},
                    {label:'结束时间',field:'end_timestamp'},
                    {label:'关键字',field:'keyword'},
                ],
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleCheck(id) {
                this.$router.push({name: '工作规划查看', params: {id: id}})
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'check':
                        this.handleCheck(row.id);
                        break;
                }
            },
        },
    }
</script>
